.joinUsBox{
    padding: 2rem;
    display: flex;
    flex-direction: row;
}

.joinUsBox>div{
    flex: 1;
}

.joinBoxLeft{
    background-color: #0B4A65;
    padding: 1rem;
    color: white;
    margin-right: 1rem;
}

.joinBoxLeft p:first-child{
    font-size: 2rem;
    padding: 2rem;
}

.joinBoxLeft p:last-child{
    padding: 2rem 3rem 2rem 2rem;
    letter-spacing: 0.1rem;
    line-height: 1.5rem;
}

.formStyle{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.formInput{
    width: 40rem;
    padding: 1rem;
    margin-bottom: 1rem;
}

.formBtn{
    background: #1E272B;
    color: white;
    border: none;
    padding: 1rem 3rem;
    cursor: pointer;
    transition: all 0.2s ease;
}

.formBtn:hover{
    background-color: #7ED4EF;
    color: black;
}