.serviceCard{
    width: 20rem;
    height: 12rem;
    background: #0B4A65;
    padding: 1rem;
    box-sizing: border-box;
    margin: 1rem;
    transition: all 0.2s ease;
    position: relative;
}

.serviceCard:hover{
    box-shadow: 0px 12px 15px -2px black;
}

.serviceCard p:nth-child(1){
    font-size: 2.2rem;
    /* height: 50%; */
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.serviceCard p{
    letter-spacing: 1px;
    font-size: 1rem;
    display: none;
}

.serviceLink{
    text-decoration: none;
    color: #fff6f6;
    /* float: right; */
    padding: 0.4rem;
    border: 1px solid white;
    /* margin-top: 2.2rem; */
    transition: all 0.2s ease;

    position: absolute;
    bottom: 1rem;
    right: 1rem;
}

.serviceLink:hover{
    color: black;
    background: white;
}