.footerContainer{
    margin-top: 1rem;
    padding: 1rem;
    width: 100vw;
    height: 25rem;
    display: flex;
    background: #1E272B;
    color: white;
}

.footerLogo{
    width: 45%;
    cursor: pointer;
}

.footerContainer>div{
    width: 25%;
    padding: 1rem;
    border-right: 1px solid;
    text-align: center;
}

.footerContainer ul{
    list-style: none;
    text-align: left;
    display: inline-block;
}

.footerContainer ul>li{
    padding: 0.5rem 0
}

.footerContainer li.footerHeading{
    font-size: 1.4rem;
    padding: 2rem 0;
    text-align: left;
}
