.mainContainer{
    background-color: #1E272B;
}

.mainText{
    width: 70%;
    height: auto;
    position: absolute;
    z-index: 1;
    top: 30%;
    padding: 5rem;
    transform: translateY(-30%);
    box-sizing: border-box;
    color: white;
    font-size: 2.5rem;
    letter-spacing: 0.3rem;

}

.mainBox{
    position: absolute;
    left: 0;
    bottom: 0;
    background: #0b4a658a;
    font-size: 1.3rem;
    color: white;
    width: 32%;
    height: 35%;
    box-sizing: border-box;
    padding: 2rem;
    border-right: 1rem solid #7ED4EF;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.mainImage{
    background-position: center;
    background-size: cover;
    width: 60%;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    opacity: 0.6;
}

.boxBtn{
    padding: 1rem;
    border: 1px solid white;
    width: 8rem;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s ease;
}

.boxBtn:hover{
    background-color: white;
    color: black;
}