.teamInfoCard{
    width: 15rem;
    height: auto;
    background: #ccc;
    position: relative;
    margin: 0 1rem 3rem 1rem;
}

.profileImg{
    width: 100%;
    height: 18rem;
    background: black;
}

.nameTag{
    width: 80%;
    padding: 0.1rem 1rem 0 1rem;
    display: flex;
    position: absolute;
    background: #1E272B;
    color: white;
    right: 0;
    flex-direction: column;
    top: 13rem;
    justify-content: space-between;
}

.name{
    font-size: 2rem;
}

.teamInfo{
    padding: 1.6rem 0rem;
    background-color: #1E272B;
}

.halfBlue{
    background: linear-gradient(180deg, #fff 30%, #1E272B 30%);
}

.profileImg{
    background-image: url(../../imgs/profile/suneet.jpg);
    background-size: cover;
    background-position: center;
}