.addressBox{
    padding: 4rem;
    width: 30rem;
    color: white;
    margin: 1rem;
    background: #1E272B;
    position: relative;
}

.addressBoxLabel{
    position: absolute;
    background: white;
    color: black;
    top: 1rem;
    right: 0;
    padding: 0.5rem 1rem;
}

.addressHeader{
    font-size: 1.2rem;
    padding: 0.7rem 0;
}

