.banner2{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 8rem;
    margin: 4rem 0 1rem 0;
}

.opaqueBox{
    display: flex;
    width: 100vw;
    height: 90vh;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    position: relative;
}


.opaqueInnerBox{
    width: 30rem;
    background: #0b4a659c;
    position: absolute;
    left: 8rem;
    top: -8rem;
}

.opaqueBoxHeader{
    position: absolute;
    width: 43rem;
    top: 0;
    padding: 8rem 1rem 1rem 1rem;
}

.opaqueBoxPara{
    padding: 16rem 4rem 4rem 3rem;
}

.rightInnerBox{
    width: 40rem;
    background: #7ED4EF;
    position: absolute;
    right: 0;
    padding: 5rem 2rem;
    bottom: 0;
}

.rightBoxHeading{
    padding-bottom: 5rem;
}