.navigation{
    display: flex;
    align-items: center;
    width: 100%;
    height: 4rem;
    background: #C4C4C4;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
}

.navigation ul{
    display: flex;
    width: 100%;
    margin: 0;
    padding: 0;
    justify-content: center;
    align-items: center;
    list-style: none;
}

.navigation ul li{
    margin: 0 1rem;
    cursor: pointer;
    transition: all 0.2s ease;
}

.navigation ul li:hover{
    color: white;
    font-size: 1.2rem;
}
