@font-face {
  font-family: 'Raleway Regular';
  font-style: normal;
  font-weight: normal;
  src: local('Raleway Regular'), url('./fonts/raleway/Raleway-Light.woff') format('woff');
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway Regular';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.hero{
  width: 100vw;
  height: 100vh;
  position: relative;
